body {
  font: 14px "Century Gothic", Futura, sans-serif;
  height:100%;
  overflow: hidden;
}

html {
  height:100%;
}

ol, ul {
  padding-left: 30px;
}

h1 {
  color: #FFFFFF;
  margin: 20px 20px;
  font-size: 3.5vw;
}

h6 {
  margin-right: 10px;
}

div.wrap {height:100%; overflow:hidden;}

.background {
  position: absolute;
  width: 100%;
  height: 120%;
  bottom: -10%;
  z-index: -2;
}

.sm-font {
  font-size: 1vw;
}

.med-font {
  font-size: 1.2vw;
}

.lrg-font {
  font-size: 1.7vw;
}

.gold-icon-sm {
  position: relative;
  height: 1vw;
  margin-right: 0.2vw;
}

.gold-icon-lrg {
  position: relative;
  height: 1.1vw;
  margin-right: 0.2vw;
  margin-bottom: 0.8vh;
}

.trait-icon {
  position: relative;
  height: 2vw;
  margin-right: 0.2vw;
}

.shop-tile {
  position: relative;
  background: #18222B;
  border: none;
  float: left;
  width: 16.66%;
  height: 23vh;
  border-radius: 5px;
}

.clickable {
  cursor: pointer;
}

.clickable:after {
  content: "";
  background: #FFFFFF;
  display: block;
  position: absolute;
  left: 0px;
  top: 0px;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: all 0.8s
}

.clickable:active:after {
  padding: 0;
  margin: 0;
  opacity: 0.5;
  transition: 0s;
}

.champ-pic {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 80%;
}

.champ-tile-window {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.champ-trait-text {
  color: #FFFFFF;
  position: absolute;
  width: 100%;
  bottom: 24%;
  left: 3%;
  line-height: 1.5;
}

.champ-tile-text {
  color: #FFFFFF;
  position: absolute;
  width: 100%;
  bottom: 19%;
}

.champ-name {
  position: absolute;
  left: 6%;
}

.champ-cost {
  position: absolute;
  right: 6%;
}

.left-btn-ui {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
}

.left-btn-text {
  position: absolute;
  left: 7%;
  top: 13%;
  height: 100%;
  width: 100%;
  line-height: 1.3;
  color: #FFFFFF;
}

.buy-xp-btn {
  position: absolute;
  height: 50%;
  width: 100%;
  top: 0px;
  text-align: left;
}

.refresh-btn {
  position: absolute;
  height: 50%;
  width: 100%;
  bottom: 0px;
  text-align: left;
}

.display-bar {
  position: relative;
  margin-bottom: 0.5%;
  padding-top: 0.5%;
}

.left-ui-corner {
  position: absolute;
  bottom: -40%;
  left: 0px;
  height: 150%;
  width: 20%;
}

.level {
  color: #FFFFFF;
  position: relative;
  bottom: -26%;
  margin-left: 1%;
}

.exp {
  color: #FFFFFF;
  position: relative;
  margin-left: 7%;
}

.reroll-odds {
  color: #FFFFFF;
  position: absolute;
  width: 30%;
  top: 7%;
  left: 17%;
}

.reroll-odds-background {
  position: absolute;
  height: 6vh;
  width: 22vw;
  z-index: -1;
}

.reroll-content {
  position: absolute;
  margin-top: 0.6vh;
  margin-left: 2.5vw;
  width: 75%;
}

.reroll-gem {
  position: relative;
  margin-bottom: 1.8%;
  margin-right: 1%;
  height: 5%;
  width: 4%;
}

.gold-background {
  position: absolute;
  bottom: -40%;
  left: 50%;
  transform: translate(-50%, 0);
  height: 120%;
  width: 10%;
}

.gold {
  color: #FFFFFF;
  font-size: 1.3vw;
  position: fixed;
  transform: translate(-50%, 0);
  left: 50%;
  top: 7.5%;
}

.shop {
  position: fixed;
  bottom: 2%;
  left: 50%;
  transform: translate(-50%, 0);
  width: 85%;
  border-radius: 5px;
}

.champ-stage-tile {
  position: relative;
  border: none;
  width: 10%;
}

.empty-tile {
  position: absolute;
  background: Transparent;
  height: 12vh;
  width: 100%;
}

.glow-when-hovered img {

 opacity: 0.8;

 /*Transition*/
 -webkit-transition: all 0.5s ease;
 -moz-transition: all 0.5s ease;
 -o-transition: all 0.5s ease;

 /*Reflection*/
 -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(.7, transparent), to(rgba(0,0,0,0.1)));
}

.glow-when-hovered img:hover {
   opacity: 1;

   /*Reflection*/
  -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(.7, transparent), to(rgba(0,0,0,0.4)));

   /*Glow*/
  -webkit-box-shadow: 0px 0px 20px rgba(255,255,255,0.8);
  -moz-box-shadow: 0px 0px 20px rgba(255,255,255,0.8);
  box-shadow: 0px 0px 20px rgba(255,255,255,0.8);
}

.stage-icon {
  position: absolute;
  width: 80%;
  height: 12vh;
  bottom: -12vh;
  border-radius: 50px;
}

.stage-star {
  position: absolute;
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 3.5vh;
  width: 80%;
  bottom: -13vh;
}

.champ-stage {
  position: fixed;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 0);
  width: 75%;
}
